import { ResourceTypeCode } from '../enums/ResourceTypeCode';
import { UseTypeCode } from '../enums/UseTypeCode';
import { RowingBoatTypeCode } from '../enums/RowingBoatTypeCode';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   Rowing boat resource type (derived from Resource) 
*/
export class RowingBoat extends BaseEntity {
 
    // Resource type 
    public resourceType: ResourceTypeCode;
 
    // Resource Id (internal ID in the club) 
    public resourceId: string;
 
    // Available for members or under repair ? 
    public isAvailable: boolean;
 
    // Resource type 
    public usedBy: UseTypeCode;
 
    // Rowing boat type (1X, @x, 2-, 4X, 4x+, 4-, 8+, 8X+ ...) 
    public subType: RowingBoatTypeCode;
 
    // Minimum boat weight [Kg] 
    public minWeight: number;
 
    // Maximum boat weight [Kg] 
    public maxWeight: number;
 
    // Manufacturer 
    public brand: string;
 
    // Number of rowers 
    public rowers: number;
 
    // Is sweeping or sculling boat ? 
    public isSweep: boolean;
 
    // Is cox required ? 
    public isCox: boolean;
 
    // Is wide boat ? 
    public isWide: boolean;
 
    // Is coastal boat ? 
    public isCoastal: boolean;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


