
/*  
*/
export class AdminBoatFindRequest {
 
    // Filter by usage allocation (empty for no filter) 
    public usedBy: string;
 
    // Filter by resource Id 
    public resourceId: string;
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    constructor(usedBy?: string, resourceId?: string, sort?: string, page?: number, pageSize?: number) { 
        this.usedBy = usedBy;
        this.resourceId = resourceId;
        this.sort = sort;
        this.page = page;
        this.pageSize = pageSize;
    }

}


